.Toolbar {
  background-color: #ededed;
  width: 100%;
  border-top: 1px solid #dbdbdb;
  border-bottom: 1px solid #dbdbdb;
  padding: 10px 0px 10px 0px;
}

.Toolbar_container {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.Filter {
  -ms-flex: 0.2 1;
      flex: 0.2 1;
}
.Search {
  -ms-flex: 2 1;
      flex: 2 1;
}
